<template>
  <div>
    <a-card title="采购订单管理">
      <a-row :gutter="16">
        <a-col v-if="mode == '批量采购模式'" :span="24" style="max-width: 200px; margin-bottom: 12px;">
          <a-input v-model="searchForm.keyWord" placeholder="关键字查询.." allowClear @pressEnter="search" />
        </a-col>

        <a-col v-if="mode == '批量采购模式'" :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="search" @click="search">查询</a-button>
        </a-col>

        <a-col v-if="mode == '批量采购模式'" :span="24" style="max-width: 200px; margin-bottom: 12px; margin-right: 10px;">
          <a-select v-model="searchForm.status" placeholder="请选择" style="width: 200px;"> 
            <a-select-option v-for="option in options_status" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>
        </a-col>

        <a-col :span="24" style="max-width: 200px; margin-bottom: 12px; margin-right: 10px;">
          <a-select v-model="searchForm.purchase_platform" placeholder="请选择" style="width: 200px;"> 
            <a-select-option v-for="option in options_platform" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>
        </a-col>

        <a-col v-if="mode == '批量采购模式'" :span="24" style="max-width: 200px; margin-bottom: 12px; margin-right: 10px;">
          <a-select v-model="searchForm.is_ask" placeholder="请选择" style="width: 200px;"> 
            <a-select-option v-for="option in options_is_ask" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>
        </a-col>

        <a-col :span="24" style="width: 100px; margin-bottom: 12px; margin-right: 40px;">
          <a-button type="primary" icon="edit" @click="changeMode">{{mode}}</a-button>
        </a-col>

        <a-col v-if="mode == '批量采购模式'" :span="24" style="width: 100px; margin-bottom: 12px; margin-right: 60px;">
          <a-button type="primary" icon="edit" @click="updatePurchaseOrder">更新待采购订单</a-button>
        </a-col>

        <a-col v-if="mode == '批量采购模式'" :span="24" style="width: 100px; margin-bottom: 12px; margin-right: 60px;">
          <a-button type="primary" icon="edit" @click="updatePurchaseOrdersIn1688">1688订单信息同步</a-button>
        </a-col>
      </a-row>

      <a-row :gutter="16" v-if="mode == '审阅模式' && searchForm.purchase_platform == '1688'" style="display: flex; align-items: center;">
        <a-col :span="24" style="width:100px; margin-bottom: 12px;">
          <span>供货商列表：</span>
        </a-col>
        <a-col :span="24" style="margin-bottom: 12px;">
          <a-button 
            v-for="item in supplier_names" 
            :key="item" type="primary" 
            style="margin-right: 10px;" 
            :class="{ 'selected-supplier': searchForm.supplier_name == item }"
            @click="getDataWithSupplier(item)">
            {{ item }}
          </a-button>
        </a-col>
      </a-row>

      <a-row :gutter="16" v-if="mode == '审阅模式' && searchForm.purchase_platform == '1688'" style="display: flex; align-items: center;">
        <a-col :span="24" style="max-width: 400px; margin-bottom: 12px;">
          <a-input v-model="postingNumber" placeholder="根据供应商添加采购订单号" allowClear @pressEnter="fillThePurchaseIdToAllOrder" />
        </a-col>

        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="edit" @click="fillThePurchaseIdToAllOrder">确定</a-button>
        </a-col>

        <a-col :span="24" style="width: 100px; margin-bottom: 12px; margin-right: 60px;">
          <a-button type="primary" icon="edit" @click="buyPurchaseProductIn1688">一键采购</a-button>
        </a-col>

      </a-row>

      <a-row :gutter="16" v-if="mode == '审阅模式' && (searchForm.purchase_platform == 'pdd' || searchForm.purchase_platform == '线下')" style="display: flex; align-items: center;">
        <a-col :span="24" style="max-width: 400px; margin-bottom: 12px;">
          <a-input v-model="postingNumber" placeholder="根据勾选框批量添加采购订单号" allowClear @pressEnter="handleSelectedRows" />
        </a-col>

        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="edit" @click="handleSelectedRows">确定</a-button>
        </a-col>
      </a-row>

      <a-row style="margin-top: 12px;">
        <a-table 
          size="small" 
          :columns="columns" 
          :dataSource="items" 
          rowKey="id" 
          :loading="loading" 
          :pagination="pagination" 
          @change="tableChange"
          :rowSelection="rowSelection" 
        >
          
          <div slot="posting_numbers" slot-scope="text, record">
            <div v-for=" postting_number,index in JSON.parse(record.posting_numbers)" :key="index+100">
              <span style="font-weight: 700;">{{ index+1 }}.</span>
              <span style="color:green"> {{ postting_number.value }} </span>
              <span style="margin-left:20px; font-weight: 700;">状态：</span>
              <span style="color:red"> {{ postting_number.signed }} </span>
              <hr style="margin-top: 0;">
            </div>
          </div>

          <div slot="is_back" slot-scope="text, record">
            <div>
              <span> {{ JSON.parse(record.error_words)?JSON.parse(record.error_words).length == 0?"无对话":JSON.parse(record.error_words)[JSON.parse(record.error_words).length-1].role == "运营"?"未处理":"已处理":"无对话"}} </span>
            </div>
          </div>

          <div slot="system_product" slot-scope="text, record">
            <div v-if="record.system_product">
              <span> SKU: </span> <span style="color: red;"> {{ record.system_product.system_sku }} </span> 
              <span> 供应商: </span> <span style="color: green;">{{ record.system_product.supplier_name }}</span>  
            </div>
          </div>

          <div slot="system_product.purchase_link" slot-scope="text, record">
            <div>
              <div v-for=" link,index in JSON.parse(record.system_product.purchase_link)" :key="index+100">
                <span>{{ index+1 }}：</span>
                <a :href="link.link" target="_blank"> {{ `链接${index+1}号` }} </a>
                <span style="color:red"> {{ link.msg }} </span>
                <hr>
              </div>
            </div>
          </div>
          
          <div slot="action" slot-scope="value, item">
            <a-button-group>
              <a-button icon="edit" size="small" @click="openAddFormModal(item)">添加单号</a-button>
              <a-button icon="edit" size="small" @click="openFormModal(item)">修改</a-button>
              <a-popconfirm title="确定删除吗" @confirm="destroy(item.id)">
                <a-button type="danger" icon="delete" size="small">作废</a-button>
              </a-popconfirm>
            </a-button-group>
          </div>
        </a-table>
      </a-row>

    </a-card>
    <form-modal v-model="visible" :form="targetItem" @update="update" />
    <AddPurchaseId v-model="AddPurchaseVisible" :form="targetItem" @update="update"/>
  </div>
</template>

<script>
import {
  getPurchaseOrderData, getWaitForPurchaseOrderSupplier, getDataInPurchaseMode, 
  upDatePurchaseOrder, fillThePurchaseIdForPurchaseOrders, cancleThePurchaseOrder, 
  updatePurchaseOrdersWithExcel,updatePurchaseOrdersIn1688WithAPI,buyPurchaseProductIn1688WithAPI} from '@/api/purchaser'
import { message } from "ant-design-vue";

export default {
  name: 'PurchaseOrder',
  components: {
    FormModal: () => import('./FormModal.vue'),
    AddPurchaseId: () => import('./AddPurchaseId.vue')
  },
  data() {
    return {
      columns: [
        {
          title: '留言',
          dataIndex: 'is_back',
          scopedSlots: { customRender: 'is_back' },
        },
        {
          title: '采购单ID',
          dataIndex: 'id',
          width: 150
        },
        {
          title: '采购单号',
          dataIndex: 'purchase_id',
        },
        {
          title: '国内运单号',
          dataIndex: 'posting_numbers',
          scopedSlots: { customRender: 'posting_numbers' },
          // width: 200
        },
        {
          title: '待采购量',
          dataIndex: 'wait_for_purchase_quantity',
        },
        {
          title: '在篮量',
          dataIndex: 'in_basket_quantity',
        },
        {
          title: '在途量',
          dataIndex: 'in_transit_quantity',
        },
        {
          title: '库存量',
          dataIndex: 'stock_quantity',
        },
        {
          title: '出库量',
          dataIndex: 'out_stock_quantity',
        },
        {
          title: '丢失量',
          dataIndex: 'loss_quantity',
        },
        {
          title: '采购价格',
          dataIndex: 'price',
        },
        {
          title: '运费',
          dataIndex: 'shipping_fee',
        },
        {
          title: '退货费用',
          dataIndex: 'back_fee',
        },
        {
          title: '物流状态',
          dataIndex: 'logistics_status',
        },
        {
          title: '采购平台',
          dataIndex: 'purchase_platform',
        },
        {
          title: '采购状态',
          dataIndex: 'status',
        },
        {
          title: '打包留言',
          dataIndex: 'packer_msg',
        },
        {
          title: '采购平台状态',
          dataIndex: 'platform_status',
        },
        {
          title: '运营咨询中',
          dataIndex: 'is_error',
          customRender: (value) => value?"是":"否",
        },
        {
          title: '系统内商品',
          dataIndex: 'system_product',
          scopedSlots: { customRender: 'system_product' },
          // width: 200,
        },
        {
          title: '采购链接',
          dataIndex: 'system_product.purchase_link',
          scopedSlots: { customRender: 'system_product.purchase_link' },
          // width: 300,
        },
        {
          title: '创建时间',
          dataIndex: 'create_time',
          // width: 300,
        },
        {
          title: '添加采购单时间',
          dataIndex: 'fill_purchase_id_time',
          // width: 300,
        },
        {
          title: '操作',
          dataIndex: 'action',
          scopedSlots: { customRender: 'action' },
          width: '156px'
        },
      ],
      
      searchForm: { start: 0, limit: 15, keyWord: "", status:"全部", purchase_platform:"全部",is_ask:"全部", supplier_name :""},
      pagination: { current: 1, total: 0, pageSize: 15 },

      loading: false,
      items: [],

      visible: false,
      AddPurchaseVisible: false,
      targetItem: {},
      form: {},
      importLoading: false,
      options_status:["全部","待采购","运输中","已完成","有异常","已作废"],
      options_platform:["全部","1688","pdd","线下"],
      options_is_ask:["全部","咨询中","未咨询"],
      supplier_names:[],
      mode:"批量采购模式",
      postingNumber:"",
      selectedRowKeys: [], // 存储选中行的key
      selectedRows: [], // 存储选中行的完整数据
    };
  },
  computed: {
    rowSelection() {
      return {
        selectedRowKeys: this.selectedRowKeys, // 当前选中的key
        onChange: this.onSelectChange, // 监听选择变化
      };
    },
  },
  watch:{
    'searchForm.purchase_platform'(newValue,oldValue){
      this.loading = true;

      if(this.mode == "批量采购模式"){
        this.searchForm.start = 0
        this.searchForm.limit = 15
        this.pagination =  {current: 1, total: 0, pageSize: 15, position:"bottom" }
        this.list()
        
      }else if (this.mode == "审阅模式"){
        this.searchForm.start = 0
        this.searchForm.limit = 1000
        this.pagination =  {current: 1, total: 0, pageSize: 1000,position:"none" }
        getWaitForPurchaseOrderSupplier(this.searchForm).then(data => {
          this.supplier_names = data.data.data
          this.searchForm.supplier_name = data.data.data?data.data.data[0]:""
          this.list()
        }).finally(() => {
          this.loading = false;
        });
      }
    },
    'searchForm.status'(newValue,oldValue){
      this.loading = true;

      if(this.mode == "批量采购模式"){
        this.searchForm.start = 0
        this.searchForm.limit = 15
        this.pagination =  {current: 1, total: 0, pageSize: 15, position:"bottom" }
        this.list()
        
      }else if (this.mode == "审阅模式"){
        this.searchForm.start = 0
        this.searchForm.limit = 1000
        this.pagination =  {current: 1, total: 0, pageSize: 1000,position:"none" }
        getWaitForPurchaseOrderSupplier(this.searchForm).then(data => {
          this.supplier_names = data.data.data
          this.searchForm.supplier_name = data.data.data?data.data.data[0]:""
          this.list()
        }).finally(() => {
          this.loading = false;
        });
      }
    },

    'searchForm.is_ask'(newValue,oldValue){
      this.loading = true;

      if(this.mode == "批量采购模式"){
        this.searchForm.start = 0
        this.searchForm.limit = 15
        this.pagination =  {current: 1, total: 0, pageSize: 15, position:"bottom" }
        this.list()
        
      }else if (this.mode == "审阅模式"){
        this.searchForm.start = 0
        this.searchForm.limit = 1000
        this.pagination =  {current: 1, total: 0, pageSize: 1000,position:"none" }
        getWaitForPurchaseOrderSupplier(this.searchForm).then(data => {
          this.supplier_names = data.data.data
          this.searchForm.supplier_name = data.data.data?data.data.data[0]:""
          this.list()
        }).finally(() => {
          this.loading = false;
        });
      }
    },
  },
  methods: {
    initialize() {
      this.list();
    },
    list() {
      this.loading = true;
      if(this.mode == "批量采购模式") {
        getPurchaseOrderData(this.searchForm).then(data => {
          this.pagination.total = data.data.count;
          this.items = data.data.data;
        }).finally(() => {
          this.loading = false;
        });
      }else{
        if (this.searchForm.purchase_platform == '1688'){
          if(this.searchForm.supplier_name){
            getDataInPurchaseMode(this.searchForm).then(data => {
              this.items = data.data.data;
            }).finally(() => {
              this.loading = false;
            });
          }else{
            message.success("暂无需要采购的订单！")
            this.changeMode()
          }
        }else{
          getPurchaseOrderData(this.searchForm).then(data => {
            this.pagination.total = data.data.count;
            this.items = data.data.data;
          }).finally(() => {
            this.loading = false;
          });
        }
        
      }
    },

    update() {
      this.AddPurchaseVisible = false
      this.visible = false
      this.list();
    },

    search() {
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },

    openFormModal(item) {
      this.targetItem = { ...item };
      this.visible = true;
    },

    openAddFormModal(item) {
      this.targetItem = { ...item };
      this.AddPurchaseVisible = true;
    },

    destroy(id) {
      cancleThePurchaseOrder({ "purchase_order_id": id }).then((res) => {
        this.$message.success(res.msg);
        this.list();
      });
    },
    tableChange(pagination, filters, sorter) {      
      this.searchForm.start = (pagination.current-1)*this.pagination.pageSize;
      this.pagination.current = pagination.current;
      this.list();
    },
    changeMode(){
      this.searchForm.start = 0
      this.pagination.current = 1;

      if(this.mode == "批量采购模式"){
        this.mode = "审阅模式"
        this.searchForm.limit = 1000
        this.pagination.pageSize = 10000
        this.pagination.position = "none"
        this.loading = true;
        getWaitForPurchaseOrderSupplier(this.searchForm).then(data => {
          this.supplier_names = data.data.data
          this.searchForm.supplier_name = data.data.data?data.data.data[0]:""
          this.list()
        }).finally(() => {
          this.loading = false;
        });
        
      }else if (this.mode == "审阅模式"){
        this.mode = "批量采购模式"
        this.searchForm.limit = 15
        this.pagination.pageSize = 15
        this.pagination.position = "bottom"
        this.list()
      }
    },
    getDataWithSupplier(item){
      this.searchForm.supplier_name = item
      this.list()
    },
    fillThePurchaseIdToAllOrder(){
      if (this.postingNumber) {
        this.loading = true;
        fillThePurchaseIdForPurchaseOrders({purchase_order_ids:this.items.map(item => item.id),purchase_id:this.postingNumber}).then(
          res => {
            this.postingNumber = ""
            message.success(res.msg)
            getWaitForPurchaseOrderSupplier(this.searchForm).then(data => {
              this.supplier_names = data.data.data
              this.searchForm.supplier_name = data.data.data?data.data.data[0]:""
              this.list()
            }).finally(() => {
              this.loading = false;
            });
          }
        )
      }else{
        message.error("采购订单号不能为空")
      }
    },
    updatePurchaseOrder(){
      upDatePurchaseOrder().then(
        res => {
          message.success(res.msg)
          this.list()
        }
      )
    },
    updatePurchaseOrdersIn1688(){
      updatePurchaseOrdersIn1688WithAPI().then(
        res => {
          message.success(res.msg)
          this.list()
        }
      )
    },
    buyPurchaseProductIn1688(){
      buyPurchaseProductIn1688WithAPI({purchase_order_msgs: this.items}).then(
        res => {
          message.success(res.msg)
          getWaitForPurchaseOrderSupplier(this.searchForm).then(data => {
              this.supplier_names = data.data.data
              this.searchForm.supplier_name = data.data.data?data.data.data[0]:""
              this.list()
            }).finally(() => {
              this.loading = false;
            });
        }
      )
    },
    // 处理勾选变化事件
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys; // 更新选中的key
      this.selectedRows = selectedRows; // 更新选中的行数据
    },
    handleSelectedRows() {
      // 获取所有选中行的ID
      const selectedIds = this.selectedRows.map(row => row.id);

      console.log(selectedIds)

      if (this.postingNumber) {
        this.loading = true;
        fillThePurchaseIdForPurchaseOrders({purchase_order_ids:selectedIds ,purchase_id:this.postingNumber}).then(
          res => {
            this.postingNumber = ""
            message.success(res.msg)
            this.list()
          }
        ).finally(() => {
          this.loading = false;
        });
      }else{
        message.error("采购订单号不能为空")
      }
    },
  },
  mounted() {
    this.initialize();
  },
  activated() {
    this.initialize();
  },
}
</script>

<style scoped>
.selected-supplier {
  background-color: #40a9ff !important; /* 选中时的背景颜色 */
  border-color: #40a9ff !important;
  color: white !important;
}
</style>